import React, { useState } from 'react'; 
import { BrowserRouter as Router, Routes, Route, Link, Navigate } from 'react-router-dom';
import CartPage from './pages/CartPage'; 
import Home from './pages/Home'; 
import ProductDetailPage from './pages/ProductDetailPage'; 
import ProductsPage from './pages/ProductsPage';
import './styles.css';

const App = () => {
  const [cartCount, setCartCount] = useState(0); // Estado para el nmero de productos en el carrito

  return (
    <Router basename="/e-commerce">
      <header>
        <Link to="/home" style={{ textDecoration: 'none', color: 'inherit' }}>
          <h1>My Store</h1>
        </Link>
        <nav>
          <ul>
            {/* Enlace a los productos */}
            <li><Link to="/products" className="nav-link">Products</Link></li>
            <li>
              <Link to="/cart" className="nav-link">
                <i className="fas fa-shopping-cart"></i>
                <span className="cart-count">{cartCount}</span> 
              </Link>
            </li>
          </ul>
        </nav>
      </header>

      <Routes>
        {/* Redirigir de / a /home cuando se carga la pgina */}
        <Route path="/" element={<Navigate to="/home" replace />} />

        {/* Pgina de inicio en /home */}
        <Route path="/home" element={<Home />} />

        {/* Pgina de productos en / */}
        <Route 
          path="/products" 
          element={<ProductsPage setCartCount={setCartCount} />} 
        />

        {/* Pgina del carrito */}
        <Route 
          path="/cart" 
          element={<CartPage setCartCount={setCartCount} />} 
        />

        {/* Pgina de detalle del producto */}
        <Route 
          path="/product/:productId" 
          element={<ProductDetailPage updateCartCount={setCartCount} />} 
        />
      </Routes>
    </Router>
  );
};

export default App;
